*{
    margin: 0;
    border: 0;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
}
.AddNewModal {
    position: fixed;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    z-index: 100;
    border: 1px solid #5680f9;
    
    &__backdrop {
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: rgba(33, 33, 33, 0.3);
    }
    &__content {
        position: relative;
        z-index: 9;
        width: 350px;
        background-color: #fff;
        padding: 10px 15px;
        border-radius: 5px;
        box-sizing: border-box;
        margin-top: -10%;
    }
    &__title {
        font-weight: 600;
        letter-spacing: 0.5px;
        line-height: 30px;
        box-sizing: border-box;
        color: #5680f9;
    }
    &__task {
        margin: 7px 0 25px;
        box-sizing: border-box;
    }
    &__input {
        line-height: 40px;
        font-size: 15px;
        width: 100%;
        border-radius: 3px;
        border: 1px solid #ccc;
        text-indent: 7px;

    }
    &__action {
        text-align: right;
    }
    &__btn {
        border: 1px solid transparent;
        width: 70px;
        box-sizing: border-box;
        line-height: 25px;
        margin: 0 7px;
        border-radius: 3px;
        font-size: 15px;
        cursor: pointer;
        &--confirm {
            background-color: #5680f9;
            color: #fff;
            border-color: #5680f9;
        }
        &--cancel {
            background-color: #fff;
            border-color: #666A95;
            color: #666A95;
        }
    }
    &__btn:last-child {
        margin-right: 0;
    }
}